export const JP_ZONE_TO_EPSG_MAP = {};

for (const [i, zone] of JP_ZONES.entries()) {
  const zoneId = 2443 + i;
  proj4.defs(`EPSG:${zoneId}`, zone);
  JP_ZONE_TO_EPSG_MAP[`${i + 1}`] = `EPSG:${zoneId}`;
}

export default proj4;
